import React,{useState} from "react";
import styles from "./ContactForm.module.css";
import Frame from "../../../assets/Frame 60.png";
import line6 from "../../../assets/Line 6.png";
import line8 from "../../../assets/Line 8.png";
import line10 from "../../../assets/Line 10.png";
import arrow from "../../../assets/arrow.svg";

const Index = () => {
  const submitForm = (event) => {
    var frm = document.getElementById("mc-embedded-subscribe-form");
    frm.submit();
    frm.reset();
    return false;
  };

  const [fname,setFname]= useState('')
  const [email,setEmail]= useState('')
  const [subject,setSubject]= useState('')
  return (
    <div className={styles.contactSection}>
      <img className={styles.vectorImg} src={Frame} alt="" />
      <div className={styles.contactDiv}>
        <div className={styles.contactContainer + " container md-mx-5"}>
          <div className={styles.contactRow + " row"}>
            <div className={styles.contactCol + " col-10 col-md-8"}>
              <h2 className={styles.contactHeading}>Contact Us</h2>
              <div id="mc_embed_signup">
                <form
                  action="https://gmail.us8.list-manage.com/subscribe/post?u=0e69c12fb72015ce767d263de&amp;id=22d5b3df2a"
                  method="post"
                  id="mc-embedded-subscribe-form"
                  name="mc-embedded-subscribe-form"
                  className="validate"
                  target="_blank"
                  noValidate
                >
                  <div className={styles.nameEmailDiv + " mb-3 "}>
                    <img className={styles.line6} src={line6} alt="" />
                    <label htmlFor="mce-FNAME" className={styles.label}>
                      FULL NAME :
                    </label><span className={styles.asteriskRed}>*</span>
                    <input
                      type="text"
                      className={
                        `${styles.inpField} ${styles.nameField}` +
                        " mc-field-group required"
                      }
                      id="mce-FNAME"
                      placeholder="Your Name"
                      aria-describedby="emailHelp"
                      name="FNAME"
                      autoComplete="off"
                      required
                      onChange={(e) => setFname(e.target.value)} 
                    />
                  </div>

                  <div className={styles.nameEmailDiv + " mb-3 "}>
                    <img className={styles.line6} src={line6} alt="" />
                    <label htmlFor="mce-EMAIL" className={styles.label}>
                      E-MAIL :
                    </label><span className={styles.asteriskRed}>*</span>
                    <input
                      type="email"
                      className={
                        `${styles.inpField} ${styles.emailField}` +
                        " mc-field-group required"
                      }
                      placeholder="Your E-Mail"
                      id="mce-EMAIL"
                      name="EMAIL"
                      autoComplete="off"
                      required
                      onChange={(e) => setEmail(e.target.value)} 
                    />
                  </div>
                  <div className={styles.nameEmailDiv + " mb-3 "}>
                    <img className={styles.line6} src={line6} alt="" />
                    <label htmlFor="mce-SUBJECT" className={styles.label}>
                      SUBJECT :
                    </label><span className={styles.asteriskRed}>*</span>
                    <input
                      type="text"
                      className={
                        `${styles.inpField} ${styles.emailField}` +
                        " mc-field-group required"
                      }
                      placeholder="What is your message about?"
                      id="mce-SUBJECT"
                      name="SUBJECT"
                      autoComplete="off"
                      required
                      onChange={(e) => setSubject(e.target.value)} 
                    />
                  </div>
                  <div className={styles.msgDiv + " "}>
                    <div className="form-group">
                      <img className={styles.line8} src={line8} alt="" />
                      <img className={styles.line10} src={line10} alt="" />
                      <label htmlFor="mce-MESSAGE" className={styles.label}>
                        YOUR MESSAGE :
                      </label>
                      <textarea
                        className={
                          `${styles.inpField} ${styles.messageField}` +
                          " mc-field-group"
                        }
                        placeholder="Your Message here"
                        id="mce-MESSAGE"
                        rows="3"
                        name="MESSAGE"
                        autoComplete="off"
                      ></textarea>
                    </div>
                  </div>
                  <div id="mce-responses" className="clear foot">
                    <div
                      className="response"
                      id="mce-error-response"
                      style={{ display: "none" }}
                    ></div>
                    <div
                      className="response"
                      id="mce-success-response"
                      style={{ display: "none" }}
                    ></div>
                  </div>
                  <div className={styles.btnDiv}>
                    <button
                    disabled={(fname && email && subject) ? false : true }
                      type="button"
                      onClick={submitForm}
                      name="subscribe"
                      id="mc-embedded-subscribe"
                      className={styles.formButton + " buttonCss"}
                    >
                      <span className={styles.formButtonText + " btnText"}>
                        SUBMIT
                      </span>
                      <img className={styles.rightArrow} src={arrow} alt="" />
                      
                    </button>
                  </div>
                </form>
              </div>              
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Index;
