const ToggleButton = ({ isExpanded, onClick }) => {
  return (
    <>
      {isExpanded ?
        <div className={styles.less} onClick={onClick}>
          <div className={styles.imgDiv}>
            <img className={styles.moreLessImg} src={less} />
          </div>
          <h5 className={styles.moreLessText}>Show Less</h5>
        </div>
        :
        <div className={styles.more} onClick={onClick}>
          <h5 className={styles.moreLessText}>Show More</h5>
          <div className={styles.imgDiv}>
            <img className={styles.moreLessImg} src={more} />
          </div>
        </div>

      }
    </>
  );
};

